
import { computed, defineComponent } from 'vue';

import { formatFullDateTime } from '~/utils/date';

export default defineComponent({
  name: 'ClubAnnouncementListing',
  props: {
    title: {
      type: String,
      required: true,
    },
    date: {
      type: String,
      default: null,
    },
    content: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const dateString = computed(() => formatFullDateTime(props.date));
    return {
      dateString,
    };
  },
});
